// @flow
import React, { useState, Fragment } from 'react';
import type { Element } from 'react';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import _get from 'lodash/get';
import { STEPS_AWAITING_SELLER_ACTION } from 'shared/Consts';
import type { Offer } from 'types/Offer';

type Props = {
  offer: Offer,
  isSubmitting: boolean,
  handleClickOpenCounterDialog: Function,
};

export const ActionButtons = ({
  offer,
  isSubmitting,
  handleClickOpenCounterDialog,
}: Props): Element<typeof Fragment> => {
  const [anchorEl, setAnchorEl] = useState(null);
  const stepId = _get(offer, ['StepId']);

  const handleClick = event => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const isInvalidSubmission =
    isSubmitting || !STEPS_AWAITING_SELLER_ACTION.includes(stepId);

  const onClickOpenCounterDialog = () => {
    if (isInvalidSubmission) return;

    handleClickOpenCounterDialog(offer);
  };

  return (
    <>
      <button
        type="button"
        className="minimal-btn animated_fastest"
        onClick={handleClick}
        disabled={isInvalidSubmission}
      >
        <i className="fa fa-ellipsis-h"></i>
      </button>
      <Menu
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <MenuItem component="button" onClick={onClickOpenCounterDialog}>
          Counter Offer
        </MenuItem>
      </Menu>
    </>
  );
};
